	padding: 1.5rem 1.5rem;
	background-color: rgba(0, 0, 0, 0.03);
}

.card-header {
	background: none;
	padding: 0.5rem 1.5rem;
	display: -ms-flexbox;
	display: flex;
	min-height: 3.5rem;
	-ms-flex-align: center;
	align-items: center;
	margin-bottom: 0;
	background-color: rgba(0, 0, 0, 0.03);
	border-bottom: 1px solid $border;
	.card-title {
		margin-bottom: 0;
	}
	&.border-0+.card-body {
		padding-top: 0;
	}
}

@media print {
	.card-header {
		display: none;
	}
}

.card-title {
	font-size: 15px;
	font-family: "roboto", sans-serif;
	font-weight: 500;
	color: #212229;
}

.reg {
	text-align: center;
	font-size: 50px;
	color: #2e1170;
	float: right;
}

.card-title {
	a {
		color: inherit;
	}
	&:only-child {
		margin-bottom: 0;
	}
	small {
		color: #8c98b9;
		font-size: 0.875rem;
		display: block;
		margin: -.75rem 0 1rem;
		line-height: 1.1;
		font-weight: 400;
	}
}

.card-subtitle {
	color: #8c98b9;
	font-size: 0.875rem;
	display: block;
	margin: -.75rem 0 1rem;
	line-height: 1.1;
	font-weight: 400;
}

.card-table {
	margin-bottom: 0;
	tr {
		&:first-child {
			td,
			th {
				border-top: 0;
			}
		}
		td:first-child,
		th:first-child {
			padding-left: 1.5rem;
		}
		td:last-child,
		th:last-child {
			padding-right: 1.5rem;
		}
	}
}

.card-body+.card-table {
	border-top: 1px solid $border;
}

.card-profile .card-header {
	height: 5rem;
	background-size: cover;
	border: 0;
}

.profie-img .flex-md-row img {
	width: 60px;
}

.card-profile-img {
