.input-group {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-ms-flex-align: stretch;
	align-items: stretch;
	width: 100%;
	> {
		.form-control,
		.custom-select,
		.custom-file {
			position: relative;
			-ms-flex: 1 1 auto;
			flex: 1 1 auto;
			width: 1%;
			margin-bottom: 0;
		}
		.form-control:focus,
		.custom-select:focus,
		.custom-file:focus {
			z-index: 3;
		}
		.form-control+ {
			.form-control,
			.custom-select,
			.custom-file {
				margin-left: -1px;
			}
		}
		.custom-select+ {
			.form-control,
			.custom-select,
			.custom-file {
				margin-left: -1px;
			}
		}
		.custom-file+ {
			.form-control,
			.custom-select,
			.custom-file {
				margin-left: -1px;
			}
		}
		.form-control:not(:last-child),
		.custom-select:not(:last-child) {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
			border-right: 0;
		}
		.form-control:not(:first-child),
		.custom-select:not(:first-child) {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
		.custom-file {
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			align-items: center;
			&:not(:last-child) .custom-file-label {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				&::after {
					border-top-right-radius: 0;
					border-bottom-right-radius: 0;
				}
			}
			&:not(:first-child) .custom-file-label {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
				&::after {
					border-top-left-radius: 0;
					border-bottom-left-radius: 0;
				}
			}
		}
	}
}

.input-group-prepend,
.input-group-append {
	display: -ms-flexbox;
	display: flex;
}

.input-group-prepend .btn,
.input-group-append .btn {
	position: relative;
	z-index: 2;
}

.input-group-prepend {
	.btn+ {
		.btn,
		.input-group-text {
			margin-left: -1px;
		}
	}
	.input-group-text+ {
		.input-group-text,
		.btn {
			margin-left: -1px;
		}
	}
}

.input-group-append {
	.btn+ {
		.btn,
		.input-group-text {
			margin-left: -1px;
		}
	}
	.input-group-text+ {
		.input-group-text,
		.btn {
			margin-left: -1px;
		}
	}
}

.input-group-prepend {
	margin-right: -1px;
}

.input-group-append {
	margin-left: -1px;
}

.input-group-text {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	padding: 0.375rem 0.75rem;
	margin-bottom: 0;
	font-size: 0.9375rem;
	font-weight: 400;
	line-height: 1.6;
	color: #3d4e67;
	text-align: center;
	white-space: nowrap;
	background-color: $white;
	border: 1px solid $border;
	border-radius: 0px;
	input {
		&[type="radio"],
		&[type="checkbox"] {
			margin-top: 0;
		}
	}
}

.input-group-addon {
	padding: .5rem .75rem;
	margin-bottom: 0;
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.25;
	color: #b7bec5;
	text-align: center;
	border: 1px solid #e3ebf3;
	border-right: 0;
	border-radius: .25rem 0 0 .2rem;
}

.input-group-addon-right {
	padding: .5rem .75rem;
	margin-bottom: 0;
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.25;
	color: #b7bec5;
	text-align: center;
	border: 1px solid #e3ebf3;
	border-radius: 0 .25rem .25rem 0;
}

.input-group-prepend,
.input-group-append,
.input-group-btn {
	font-size: 0.9375rem;
}

.input-group-prepend>.input-group-text {
	border-right: 0;
}

.input-group-append>.input-group-text {
	border-left: 0;
}

.input-icon {
	position: relative;
	.form-control {
		&:not(:last-child),
		&:not(:first-child) {
			padding-left: 2.5rem;
		}
	}
}

.input-icon-addon {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	color: $white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-pack: center;
	justify-content: center;
	min-width: 2.5rem;
	pointer-events: none;
	&:last-child {
		left: auto;
		right: 0;
	}
}

.input-group> {
	.input-group-prepend> {
		.btn,
		.input-group-text {
			border-top-right-radius: 3px;
			border-bottom-right-radius: 3px;
		}
	}
	.input-group-append {
		&:not(:last-child)> {
			.btn,
			.input-group-text {
				border-top-right-radius: 3px;
				border-bottom-right-radius: 3px;
			}
		}
		&:last-child> {
			.btn:not(:last-child):not(.dropdown-toggle),
			.input-group-text:not(:last-child) {
				border-top-right-radius: 3px;
				border-bottom-right-radius: 3px;
			}
		}
	}
}